import { useEffect, useState } from 'react'
import cn from 'classnames';

import { LearningCard } from '../cards/LearningCard'
import ABC from '../json/abc.json'

function Training() {
  const [stateCollection, setCollection] = useState('h')
  const [stateCollectionData, setCollectionData] = useState([])

  useEffect(() => {
    switch (stateCollection) {
      case 'h':
        setCollectionData(ABC.map(item => ({ kana: item.jp.h, transliteration: item.en })))
        break;
      case 'k':
        setCollectionData(ABC.map(item => ({ kana: item.jp.k, transliteration: item.en })))
        break;
      case 'hk':
        setCollectionData(ABC.map(item => ({ kana: `${item.jp.h}|${item.jp.k}`, transliteration: item.en })))
        break;
      default:
        setCollectionData([])
        break;
    }
  }, [stateCollection])
  return (
    <>
      <div className='flex w-min mb-4 border border-gray-300 rounded'>
        <div
          className={cn(
            'p-4 border-r border-gray-300 cursor-pointer',
            {
              'bg-gray-200': stateCollection === 'h'
            }
          )}
          onClick={() => setCollection('h')}
        >Hiragana</div>
        <div
          className={cn(
            'p-4 border-r border-gray-300 cursor-pointer',
            {
              'bg-gray-200': stateCollection === 'k'
            }
          )}
          onClick={() => setCollection('k')}
        >Katakana</div>
        <div
          className={cn(
            'p-4 cursor-pointer',
            {
              'bg-gray-200': stateCollection === 'hk'
            }
          )}
          onClick={() => setCollection('hk')}
        >Hiragana|Katakana</div>
      </div>
      <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4'>
        {
          stateCollectionData.map((card, idx) => (
            <LearningCard
              key={idx}
              data={card}
            />
          ))
        }
      </div>
    </>
  )
}

export default Training