import { useEffect, useState } from 'react'
import cn from 'classnames';
import { TrainingCard } from '../cards/TrainingCard'
import ABC from '../json/abc.json'

// https://javascript.info/task/shuffle
function shuffle(array) {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }

  return array
}

function pick(arr, exclude) {
  if (!Array.isArray(exclude)) {
    return arr[Math.floor(Math.random() * arr.length)]
  } else {
    const p = arr[Math.floor(Math.random() * arr.length)]

    if (exclude.indexOf(p) === -1) {
      return p
    } else {
      return pick(arr, exclude)
    }
  }
}

function Training() {
  const [stateAttempt, setAttempt] = useState(0)
  const [stateCorrectAnswers, setCorrectAnswers] = useState(0)
  const [statePick, setPick] = useState({options: []})
  const [stateCollection, setCollection] = useState('h')
  
  useEffect(() => {
    const p = pick(ABC)
    let options = [p]

    for (let i = 0; i < 2; i++) {
      options.push(pick(ABC, options));
    }
    // console.table(options);
    options = options.map((item, idx) => ({
      value: item.en,
      isCorrect: !idx
    }))
    // console.table(options);
    
    options = shuffle(options)
    // console.table(options);

    switch (stateCollection) {
      case 'h':
        setPick({
          kana: p.jp.h,
          transliteration: p.en,
          options
        })
        break;
      case 'k':
        setPick({
          kana: p.jp.k,
          transliteration: p.en,
          options
        })
        break;
      case 'hk':
        setPick({
          kana: Math.random() > 0.5 ? p.jp.h : p.jp.k,
          transliteration: p.en,
          options
        })
        break;
      default:
        setPick({})
        break;
    }
  }, [stateAttempt, stateCollection])

  function checkAnswerHandler(isCorrect) {
    setAttempt(stateAttempt + 1)
    if (isCorrect) {
      setCorrectAnswers(stateCorrectAnswers + 1)
    }
  }

  return (
    <>
      <div>Attempts count: { stateAttempt }</div>
      <div>Correct answers count: { stateCorrectAnswers }</div>
      <div className='flex w-min mb-4 border border-gray-300 rounded'>
        <div
          className={cn(
            'p-4 border-r border-gray-300 cursor-pointer',
            {
              'bg-gray-200': stateCollection === 'h'
            }
          )}
          onClick={() => setCollection('h')}
        >
          Hiragana
        </div>
        <div
          className={cn(
            'p-4 border-r border-gray-300 cursor-pointer',
            {
              'bg-gray-200': stateCollection === 'k'
            }
          )}
          onClick={() => setCollection('k')}
        >
          Katakana
        </div>
        <div
          className={cn(
            'p-4 cursor-pointer',
            {
              'bg-gray-200': stateCollection === 'hk'
            }
          )}
          onClick={() => setCollection('hk')}
        >
          Both
        </div>
      </div>
      <div className='flex justify-center'>
        <div className='w-full md:w-1/2 lg:w-1/3'>
          <TrainingCard
            data={statePick}
            checkAnswerHandler={checkAnswerHandler}
          />
        </div>
      </div>
    </>
  )
}

export default Training