import cn from 'classnames';
function TrainingCard({
  data = [],
  checkAnswerHandler,
}) {
  return (
    <div
      className='border border-indigo-600 rounded w-full'
    >
      <div className='flex justify-center text-8xl m-4 p-1'>
        { data.kana }
      </div>
      <div className='flex border-t border-indigo-600'>
        {
          data.options.map((option, idx) => (
            <div
              key={idx}
              className={cn(
                'w-1/3 py-3 text-center cursor-pointer hover:bg-indigo-100',
                {
                  'border-l border-indigo-600': idx > 0
                }
              )}
              onClick={() => checkAnswerHandler(option.isCorrect)}
            >{ option.value }</div>    
          ))
        }
      </div>
    </div>
  )
}

export { TrainingCard }